/* add a black background to the class "topnav" */
@import url('https://fonts.googleapis.com/css?family=Muli&display=swap');
@import url('https://fonts.googleapis.com/css?family=Quicksand&display=swap');



#topnav {
    background: linear-gradient(180deg, rgb(5, 4, 8) 10%, rgb(24, 27, 30) 100%);
    
    color: white; /* Text color */
    border-color: white;
    border-style: solid;
    border-right: 0px; 
    border-left: 0px; 
    border-top: 0px; 
    border-width: 1px;

    z-index: 1000; /* Ensure it's on top of other content */
    position: fixed; /* Fix the navbar position */
    top: 0; /* Position it at the top of the viewport */
    width: 100%; /* Take the full width of the viewport */


    display: flex;
    padding-left: 2em;
    padding-right: 2em;
    
    .navleft {
        float: left; /* Float the left part of the navbar to the left */
        display: grid;
        place-items: left;
        padding: 2%;
    }
    
    .navbuttons {
        display: flex;
        flex-grow: 1;
        justify-content: right;
        float: right; /* Float the right part of the navbar to the right */
        padding-right: 7.5%;
    }
}

#content{
    display:flex;
    flex-direction: column;
    place-items: center;
    text-align: center;
    flex-grow: 1;
}

#aboutparagraph{
    padding-bottom: 20%;
    padding-top: 10%;
    text-align: justify;
}



/*disable scroll bar*/
body::-webkit-scrollbar {
    display: none;
}

html {
    scroll-behavior: smooth;
  }


.hidden{
    opacity: 0;
    filter:blur(5px);
    transform: translateX(-100%);
    transition: all 0.7s;
    
}

.show{
    opacity: 1;
    filter: blur(0);
    transform: translateX(0);
    font-family: 'Quicksand', sans-serif;
    font-weight: 700;
    font-style: normal;
    font-size: 38px;
    line-height: 1.15;
    letter-spacing: -.02em;
    -webkit-font-smoothing: antialiased;
}

footer{
    display: flex;
    flex-direction: column;
    text-align: right;
    font-family: 'Quicksand', sans-serif; 
    font-weight: 700;
    font-style: normal;

    line-height: 1.15;
    letter-spacing: -.02em; 
    -webkit-font-smoothing: antialiased;
    padding-bottom: 2%;    
    padding-right: 5%;
    background: (rgb(5, 4, 8) 10%,);

    border-color: white;
    border-style: solid;
    border-right: 0px; 
    border-left: 0px; 
    border-bottom: 0px; 
    border-width: 1px;
    height:10%;
}

section{
    padding: 2em;
}

body {
    font-family: 'Muli', sans-serif;
    height: 100vh;
    margin: 0;
    background: linear-gradient(0deg, rgb(5, 4, 8) 10%, rgb(24, 27, 30) 100%);
    color:white;
    justify-content: center;
}

button{
    background-color: transparent;
    color: white;
    border: none;
    cursor: pointer;
    text-decoration: none;
    font-size: 38px;
    font-family: 'Quicksand', sans-serif;    
}